.toc {
	margin: 2em 0;
	padding: 1em 0;
	border: 1px solid #eee;
	border-radius: 4px;
	box-shadow: 0 1px 6px rgba(0,0,0,0.03);
	h2 {
		margin-left: 1em;
    border-bottom: 1px solid beige;
    span {
    	font-size: 14px;
    	display: inline-block;
    	cursor: pointer;
    }
	}
}

.meta {
	color: #777;
	a {
		color: inherit;
		&:hover {
			color: cadetblue;
		}
	}
	span {
		display: inline-block;
		margin: 5px;
	}
}

.tags {
	a {
		border: none;
    color: rgba(0,0,0,.68);
    background: rgba(0,0,0,.05);
    font-weight: 400;
    border-radius: 3px;
    padding: 5px 10px;
    margin-right: 8px;
    margin-bottom: 8px;
	}
}